.search {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
  }
  .search__submit {
    padding: 5px;
    background-color: rgba(165, 42, 42, 0.397);
    color: black;
    border-radius: 7px;
    width: 80px;
    margin-left: 5px;
    cursor: pointer;
    
  }
  .search__submit:hover {
    background-color: #282c34;
    color: antiquewhite;
  }
  .search__text {
    width: 60%;
    min-width: 170px;
    border-radius: 7px;
    text-align: right;
    padding-right: 20px;
    background-image: url(../img/search.png);   
    background-repeat: no-repeat;
    background-position: 4px; 
    background-size: contain;
    outline: none; 
  }

  .search__virtkey {
    border-radius: 7px;
    margin-left: 5px;
    cursor: pointer;
  }

  .virtkey__img {
    display: block;
    height: 25px;
  }



  @media screen and (min-width: 652px) and (max-width: 776px) {
    .search__virtkey  {
        display: none;
    }
  }

@media screen and (max-width: 651px) {
  .search__virtkey {
      display: none;
    }
  }