.movies {
    display: block;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow:  ellipsis;
    min-height: 462px;
}

.poster {
  padding-left: 4vw;
    max-width: 25%;
    display: none;
    white-space: normal;
    cursor: pointer;
}
//:enth-of-type(1)
.active {
  display: inline-block; 

}

@media screen and (min-width: 694px) and (max-width: 915px) {
    .poster {
      max-width: 33%;
    }
  }
  @media screen and (min-width: 652px) and (max-width: 693px) {
    .poster {
      max-width: 50%;
    }
  }
  @media screen and (max-width: 651px) {
    .poster {
      max-width: 100%;
      margin: auto;
    }
  }

  .intro {
    font-size: large;
  }

  .error-message {
    font-size: large;
    color: red;
  }

  .poster__img {
      width: 200px;
      border-radius: 7px;
  }

  .star {
    display: inline;
    height: 20px;
    width: 20px;
}

.rating {
  white-space: nowrap;
  display: inline;
}