.header {
    background-color: #282c34;
    height: 70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: calc(10px + 2vmin);
    color: white;
    padding: 20px;
}

.header__help {
    color: brown;
    border-radius: 50%;
    background-color: yellow;
    font-size: calc(15px + 2vmin);
}