   .spinner__span {
   width: 20px;
   height: 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0px;
    margin: auto;
  
    border: 3px solid #f3f3f3; 
    border-top: 3px solid rgba(0,0,0, .5); 
    border-radius: 50%;
    animation: spin .5s linear infinite;
  }
  
  .spinner {
    position: relative;
     left: -23px;
     top: 0;
     right: 0;
     bottom: 0;
     background: #00a5f1;
     z-index: 5;
   }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }