.footer {
    background-color: #282c34;
    height: 70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: calc(10px + 2vmin);
    color: white;
    padding: 12px 30px 12px 30px;  
    margin: auto;
    margin-top: 7px;
}

.github__icon {
    width: 30px;
    height: 30px;
    filter: invert(100%);
}
.github {
    cursor: pointer;
}

.school {
    cursor: pointer;
}
