.keyboard {
    position: fixed;
    top: 200px;
    left: 10%;
    font-family: 'Arial', sans-serif;
    font-size: 29px;
    font-weight: bold;
    height: auto;
   
    background-color: rgba(245, 245, 245, 0.849);
}

.keyboard__key {
    position: relative;
    display: block;
    width: 35px;
    height: 35px;
    border: 1px solid black;
    border-radius: 5px;
    margin: 4px;
    padding: 2px;
    padding-top: 0;
    float: left;
    text-align: center;
    box-shadow: 3px 3px 2px  brown;
    cursor: pointer;
}
.keyboard__key:hover{
    background: grey;
}
.key__active {
     box-shadow: -5px -5px 2px -5px brown;

     top: 3px;
     left: 3px;
 }

.backquote  {
    width: 30px;
}
.backspace {
    width: 95px;
    font-size: 16px;
}
.tab {
    width: 90px;
}
.capslock {
    width: 80px;
    font-size: 14px;
}
.enter{
    width: 90px;
}
.shiftleft {
    width: 80px;
    font-size: 18px;
}
.controlleft {
    width: 80px;
    font-size: 18px;
}
.shiftright {
    width: 90px;
    font-size: 18px;
}
.space {
    width: 245px;
}
    
.altleft {
    font-size: 18px;
  }
  
.altright {
    font-size: 18px;
}
.controlright {
    font-size: 18px;
    white-space: pre-line;
    width: 40px;
}

.clearfix {
    display: block;
    width: 100%;
    clear: both;
}

.ruen {
    width: 90px;

}
@media screen and (min-width: 652px) and (max-width: 776px) {
    .keyboard  {
        display: none;
    }
  }

@media screen and (max-width: 651px) {
    .keyboard {
      display: none;
    }
  }