.help {
    top:0;
    left: 0;
    
    position: fixed;
    background-color: rgba(100, 148, 237, 0.295);
    z-index: 20;
    width: 100%;
    height: 100%;
    text-align: center;
}

.help__text {
    position: absolute;
    top: 10vh;
    left: 5vw;
    margin: auto;
    padding: 5vh;
    background-color: cornsilk;
    width: 90%;
    height: 80%;
    text-align: left;
   
    display: flex;
   flex-direction: column;
   align-content: center;
}

.help__close {
    align-items: center;
    display: block;
    margin: auto;
    position: relative;
}


@media screen and (min-width: 694px) and (max-width: 915px) {
    .help {
        font-size: 1.6rem;
    }
  }
  @media screen and (min-width: 652px) and (max-width: 693px) {
    .help {
        font-size: 1.2rem;
    }
  }
  @media screen and (max-width: 651px) {
    .help {
        font-size: 0.9rem;
    }
  }